/**
 * @packageDocumentation
 * @module Serializer
 */

import createSerializer from './createSerializer';
import type { MoOptions, ServerMo } from './type';
import type {
  MoSerializationPluginFactory as PluginFactory,
} from '@ali/4ever-cangjie';

import plugins from './plugins/valueJsonML';
import createCustomizedMoPlugins from './createCustomizedMoPlugins';

export const serverPlugins = plugins;

export const createServerMoPlugins = createCustomizedMoPlugins(plugins as unknown as Record<string, PluginFactory>);
/**
 * 序列化工具，仅适用于 jsonMLToValue 与 valueToJsonML
 */
function Mo(options?: MoOptions) {
  const moPlugins = options?.plugins || createServerMoPlugins();
  return createSerializer(moPlugins, options?.parser) as ServerMo;
}

export const moServerSerializer = Mo({ plugins: createServerMoPlugins() });

export default Mo;

